.statistics-start {
  text-align: center;

  &__container {
    padding-top: 20px;
    padding-bottom: 40px;
  }

  &__title {
    margin-bottom: 40px;
    font-size: 16px;
    letter-spacing: 0.8px;
    text-align: center;

    &:before {
      content: '';
      display: block;
      width: 200px;
      height: 200px;
      margin: 0 auto 43px;
      background: url(../img/app-device.png) no-repeat center;
    }
  }

  &__title--connection {
    font-size: 18px;

    &:before {
      background: url(../img/app-connection.png) no-repeat center;
    }
  }

  &__list {
    margin-bottom: 40px;
  }

  &__text {
    letter-spacing: 0.7px;
    color: $gray;
  }
}
