.page-qr {
  $good: #b8d678;
  $bad: #d55242;
  $neutral: #f4a257;
  $bg: #f7fafc;
  $primary: #74ade6;
  $shadow-color: #c0c7d4;
  $bg-footer: #b2c4d5;

  background: $bg;
  flex-grow: 1;

  .container {
    padding-top: 50px;
    padding-bottom: 50px;
    height: 100%;
  }

  &__rate {
    max-width: 300px;
    margin: 0 auto;
  }

  &__rate-message {
    position: relative;
    padding: 55px 45px;
    font-size: 23px;
    font-weight: 500;
    letter-spacing: 0.58px;
    text-align: center;
    color: #fff;
    border-radius: 10px;
    border: solid 7px $primary;
    box-shadow: 0 11px 20px 0 $shadow-color;
    background: $primary;

    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      bottom: -36px;
      left: -7px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 18px;
      border-color: $primary transparent transparent $primary;
    }

    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      bottom: -18px;
      left: 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 18px;
      border-color: $primary transparent transparent $primary;
    }
  }

  &__rate-message-success {
    min-height: 200px;
    margin-top: 140px;
    padding-top: 40px;
    font-size: 23px;
    font-weight: 500;
    letter-spacing: 0.58px;
    text-align: center;
    color: #000000;
    background: url(../img/qr-check.png) no-repeat top center;
  }

  &__rate-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 65px 0 30px;
  }

  .qr-btn {
    font-size: 0;
    display: block;
    width: 98px;
    height: 99px;
    background: transparent;
    background-image: url(../img/qr-sprite.png);
    background-repeat: no-repeat;
    border: none;

    &:active {
      transform: translateY(2px);
    }

    &:active, &:focus {
      border: none;
      outline: none;
    }

    &--bad {
      background-position: -5px -5px;
      &:active {
        background-position: -106px -5px;
      }
    }

    &--neutral {
      background-position: -409px -5px;
      &:active {
        background-position: -510px -5px;
      }
    }

    &--good {
      background-position: -207px -5px;
      &:active {
        background-position: -308px -5px;
      }
    }
  }

  &__footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    border-bottom: 10px solid $primary;
    text-align: right;
    font-size: 19px;
    color: $bg-footer;

    b {
      line-height: 3;
      margin: 0 20px;
    }
  }
}
