.page-datepicker {

  // should be delete
  .react-datepicker__tab-loop {
    display: none;
    position: absolute;
  }

  // should be delete
  .react-datepicker-popper {
    position: absolute;
    top: 0;
    left: 0;
  }

  input {
    width: 190px;
    padding: 8.5px 13px;
    font-size: $font-size-base;
    letter-spacing: 0.7px;
    text-align: center;
    color: $gray-dark;
    border-radius: 3px;
    border: 1px solid $gray-lighter;
    background-color: $white;

    &:focus {
      outline: none;
    }

    @include media-breakpoint-up(md) {
      width: 140px;
      padding-top: 3.5px;
      padding-bottom: 3.5px;
    }
  }

  .react-datepicker-wrapper {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    @include media-breakpoint-up(md) {
      margin-bottom: 0;
      margin-right: 8px;

      &:last-child {
        margin-right: 12px;
      }
    }
  }

  .react-datepicker {
    box-shadow: 0 30px 40px 0 $gray-light;

    &__navigation {
      top: 24px;

      &:focus {
        outline: none;
      }
    }

    &__header {
      padding-top: 20px;
      border-bottom: none;
    }

    &__current-month {
      margin-bottom: 20px;
      font-size: 1rem;
      font-weight: normal;
      letter-spacing: 0.8px;
    }

    &__month {
      margin: 0;
      padding: 15px 15px 25px 13px;
    }

    &__week {
      margin-bottom: 5px;
      font-size: 0;
    }

    &__day {
      font-size: 1rem;
      border-radius: 0;

      &:focus {
        outline: none;
      }

      &--in-range {
        background-color: #f2f6f9;
        color: $gray-dark;
      }

      &--range-start,
      &--range-end {
        background-color: $blue;
        color: $white;
      }
    }

    &__day-names {
      font-size: 0;
    }

    &__day-name {
      font-size: $font-size-base;
      letter-spacing: 0.7px;
      color: #b2c4d5;
    }
  }


}
