.main-page {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  min-height: 780px;
  flex-grow: 1;
  background-color: #eefaff;

  .container {
    @include media-breakpoint-up(xl) {
      max-width: 1240px;
    }
  }

  .carousel {
    flex-grow: 1;
  }

  .carousel-inner {
    height: 100%;
    padding-top: 80px;

    @include media-breakpoint-up(xl) {
      padding-top: 0;
    }
  }

  .carousel-item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    @include media-breakpoint-up(xl) {
      &.is-happy {
        background: url(../img/slide-1.jpg) no-repeat center 40% ;
      }

      &.is-indifferent {
        background: url(../img/slide-2.jpg) no-repeat center 40% ;
      }

      &.is-angry {
        background: url(../img/slide-3.jpg) no-repeat center 40%;
      }
    }
  }

  &__logo {
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;

    @include media-breakpoint-up(xl) {
      transform: none;
      left: 0;
      width: 100%;
    }
  }
}


.main-page-footer {
  font-family: 'Montserrat', sans-serif;
  background-color: #ffffff;
  color: #485766;

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;

    @include media-breakpoint-up(xl) {
      max-width: 1240px;
      padding-top: 25px;
      padding-bottom: 25px;
      flex-direction: row;
      align-items: baseline;
      justify-content: flex-start;
    }
  }

  p {

    @include media-breakpoint-up(xl) {
      margin-bottom: 0;
      margin-right: 80px;
      font-size: 16px;

      &:last-child {
        margin-right: 0;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.has-logo {
      position: relative;
      padding-left: 25px;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        width: 20px;
        height: 20px;
        background: url(../img/logo-sm.png) no-repeat center;
        background-size: contain;
      }
    }

    a {
      color: #485766;

      &:hover, &:focus, &:active {
        text-decoration: none;
        color: #485766;
      }
    }
  }
}

.main-slide {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  color: #040830;

  @include media-breakpoint-up(xl) {
    text-align: left;
  }

  &__title {
    margin-bottom: 20px;
    font-weight: bold;

    @include media-breakpoint-up(xl) {
      margin-bottom: 50px;
      font-size: 24px;
    }

    span {
      display: block;
      font-weight: 900;
      font-size: 55px;
      text-transform: uppercase;

      @include media-breakpoint-up(xl) {
        margin-top: 20px;
        font-size: 100px;
      }
    }
  }

  &__text {
    margin-bottom: 20px;

    @include media-breakpoint-up(xl) {
      margin-bottom: 60px;
      font-size: 22px;
    }
  }

  &__btn {
    margin-bottom: 35px;

    @include media-breakpoint-up(xl) {
      margin-bottom: 0;
    }

    .btn {
      border: solid 2px #f3cb25;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 0.9px;
      color: #050931;

      @include media-breakpoint-up(xl) {
        min-width: 190px;
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: 18px;
      }

      &:hover {
        background-color: #f3cb25;
      }

      &:focus {
        box-shadow: none;
      }
    }
  }

  &__img-xs {
    @include media-breakpoint-up(xl) {
      display: none;
    }
  }
}
